<template>
	<view @touchstart="chumo_kaishi" @touchend="chumo_jieshu" style="width: 100vw;height: 100vh;">
		<debug v-if="debug_show"></debug>

		<!-- <button @click="moban()">123</button> -->

		<view class="box" @click="tiaozhuan_qiehuan_xiaoqu()">
			<view class="cu-bar search bg-white">
				<view class="search-form round">
					<text class="cuIcon-search"></text>
					<input type="text" placeholder="搜索小区" confirm-type="search"></input>
				</view>
				<view class="action">
					<button class="cu-btn bg-blue shadow-blur round">搜索</button>
				</view>
			</view>
		</view>





		<!-- 轮播图 -->
		<swiper class="screen-swiper huxing_quan" style="height: 28vh;width: 96vw;margin-left: 2vw;background: #FFFFFF;"
		 :class="dotStyle?'square-dot':'round-dot'" :indicator-dots="true" :circular="true" :autoplay="true" interval="5000"
		 duration="500">
			<swiper-item @click="tiaozhuan_tuwen([item.noticeId])" v-if="item.noticeType == 1" v-for="(item,index) in lunbo_list"
			 :key="index" style="border-radius:15px;background: #FFFFFF;">
				<image :src="item.bannerUrl" mode="aspectFill"></image>
			</swiper-item>
		</swiper>
		<!-- 轮播图 -->


		<view class="hengfang" style="width: 100vw;margin-top: 2vw;">
			<view v-for="item in leibie" :key="item.value" style="background: #ffffff;flex: 1;">
				<view @click="tiaozhuan_yemian([item.url])" style="padding-top: 2vw;padding-bottom: 2vw;">
					<view style="width: inherit;margin-left: 5vw;">
						<image style="width: 15vw;height: 15vw;border-radius:0%" :src="item.img"></image>
					</view>
					<view style="width: inherit;text-align: center;margin-top: 10rpx;font-size: 80%;">{{ item.text }}</view>
				</view>
			</view>
		</view>

		<!-- 		<view class="hengfang" style="height: 8vw;background: #FFFFFF;margin-top: 2vw;">
			<view style="width: 10vw;line-height: 8vw;"><text class="cuIcon-notificationfill" style="color: #39b54a;margin-left: 3vw;"></text></view>
			<view style="width: 80vw;line-height: 8vw;text-align: left;color: #7b7b7b;">祝各位业主2021年，新村快乐！</view>
			<view style="width: 10vw;line-height: 8vw;"><text class="cuIcon-right" style="color: #a1a4a9;margin-left: 3vw;"></text></view>
		</view> -->

		<view style="margin-left: 3vw;font-size: 150%;font-weight: 900;margin-top: 4vw;margin-bottom: 4vw;color: #5f5f5f;">
			最近通知<text style="font-size: 54%;font-weight: 500;margin-left: 4vw;color: #c2c2c2;">社区公告、身边大小事</text>
		</view>


		<view v-if="item.noticeType == 2" @click="tiaozhuan_tuwen([item.noticeId])" style="width: 100vw;" v-for="(item,index) in xinwen_list"
		 :key="index">
			<view style="font-size: 120%;margin-left: 3vw;font-weight: 600;">{{ item.noticeTitle }}</view>
			<view class="hengfang" style="margin-top: 1vw;margin-bottom: 1vw;color: #c2c2c2;">
				<view style="width: 79vw;margin-left: 3vw;">{{ item.createBy }}<text style="margin-left: 2vw;margin-right: 2vw;">|</text>{{ item.createTime }}</view>
				<view style="width: 18vw;"><text class="cuIcon-footprint">10086</text></view>
			</view>
			<image style="width: 96vw;height: 40vw;margin-left: 2vw;" class="huxing_quan_mini" mode="aspectFill" :src="item.bannerUrl"></image>
			<view style="width: 96vw;font-size: 100%;margin-top: 1vw;margin-left: 2vw;color:#c2c2c2 ;display: -webkit-box;-webkit-box-orient: vertical;-webkit-line-clamp: 3;overflow: hidden;">
				{{ item.noticeContent}}
			</view>
			<view style="width: 100vw;height: 12rpx;background: #F1F1F1;margin-top: 1vw;"></view>
		</view>

		<view v-if="false" class="cu-card article duan" style="padding-bottom: 0rpx;" v-for="(item,index) in xinwen_list"
		 :key="index">
			<view v-if="item.noticeType == 2" @click="tiaozhuan_tuwen([item.noticeId])" class="cu-item shadow" style="margin: 15rpx;">
				<view class="title">
					<view class="text-cut">{{ item.noticeTitle }}</view>
				</view>
				<view class="content">
					<view class="desc">
						<view class="text-content">{{ item.noticeContent}}</view>
						<view>
							<view class="cu-tag bg-red light sm round">发布人：{{ item.createBy }}</view>
							<view class="cu-tag bg-green light sm round">{{ item.createTime }}</view>
							<view style="margin-top: 1vw;"><text class="cuIcon-creative">100</text><text class="cuIcon-footprint" style="margin-left: 1vw;">100</text><text
								 class="cuIcon-mark" style="margin-left: 1vw;">100</text></view>
						</view>
					</view>
					<image style="height: 28vw;" :src="item.bannerUrl" mode="aspectFill"></image>
				</view>

			</view>
		</view>

	</view>
</template>

<script>
	import debug from '../../components/debug.vue';
	export default {
		components:{
			debug
		},
		data() {
			return {
				leibie: [{
						img: '/static/png/jiaofei.png',
						text: '快速缴费',
						url: '../modular/jiaofei_list/jiaofei_list'
					},
					{
						img: '/static/png/baoxiu.png',
						text: '快速报修',
						url: '../modular/baoxiu_ing/baoxiu_ing'
					},
					{
						img: '/static/png/fankui.png',
						text: '快速反馈',
						url: '../modular/fankui_ing/fankui_ing'
					},
					{
						img: '/static/png/jiating.png',
						text: '成员管理',
						url: '../modular/geren_jiating/geren_jiating'
					},
				],
				dotStyle: true,
				start_x: 0,
				start_y: 0,
				end_x: 0,
				end_y: 0,
				xiaoqu_list: {},
				lunbo_list: [],
				xinwen_list: [],
				debug_show:false,
			};
		},
		onLoad() {

			let _this = this

			_this.weixin_logn()

			// uni.navigateTo({
			// 	url:"../xiche/dingdan_xiangqing/dingdan_xiangqing"
			// })

			// let _this = this
			// let data = {
			// }
			// _this.wangluo_qingqiu('index',null,false,true,'GET').then(value =>{
			// 	if(value.length){

			// 	}else{
			// 		// uni.showModal({
			// 		// 	title: '提示',
			// 		// 	content: '没有数据',
			// 		// 	showCancel: false,
			// 		// 	success:function(){
			// 		// 		_this.list = []
			// 		// 	}
			// 		// });
			// 	}
			// } )


		},
		methods: {
			tiaozhuan_yemian: function(res) {
				if (getApp().globalData.public_user_property) {
					uni.navigateTo({
						url: res[0]
					})
				} else {
					uni.showModal({
						title: '提示',
						content: "请绑定房产后操作！",
						showCancel: false,
						success: function() {
							uni.navigateTo({
								url: "../modular/wode_bangding/wode_bangding"
							})
						}
					});

				}
			},
			tiaozhuan_qiehuan_xiaoqu: function() {
				uni.navigateTo({
					url: "../modular/xuanze_xiaoqu/xuanze_xiaoqu"
				})
			},
			tiaozhuan_tuwen: function(res) {
				uni.navigateTo({
					url: "../modular/shequ_gonggao/shequ_gonggao?id=" + res[0]
				})
			},
			moban: function() {

				// uni.requestSubscribeMessage({
				// 	tmplIds: ['_hzoVWaIKue7EYxYx61SxUbkheamwMeH-mm2d8eor40'],
				// 	  success (res) { 
				// 		  console.log(res)
				// 		  },
				// 	  fail(res){ 
				// 		   console.log(res)
				// 	  }
				// })

				let _this = this
				uni.login({
					provider: 'weixin',
					success(res) {
						let data = {
							code: res.code
						}
						_this.wangluo_qingqiu('user/wx97bdba35b41dc3aa/binding', data, false, true, 'POST').then(value => {
							if (value) {

							}
						})
					}
				})

			},
			chumo_kaishi: function(e) {
				return
				// console.log('kaishi')
				this.start_x = e.changedTouches[0].clientX
				this.start_y = e.changedTouches[0].clientY
			},
			chumo_jieshu: function(e) {
				return
				// console.log('jieshu')

				this.end_x = e.changedTouches[0].clientX
				this.end_y = e.changedTouches[0].clientY

				let _this = this
				let kaishi_x = _this.start_x
				let kaishi_y = _this.start_y
				let jieshu_x = _this.end_x
				let jieshu_y = _this.end_y

				let cha_x = jieshu_x - kaishi_x
				let cha_y = jieshu_y - kaishi_x

				if (cha_x > 150) {

					// uni.showModal({
					// 	title:"左滑"
					// })
					uni.reLaunch({
						url: "../index_4/index_4"
					})

				} else if (cha_x < -150) {

					// uni.showModal({
					// 	title:"右滑"
					// })

					uni.reLaunch({
						url: "../index_3/index_3"
					})
				}

			},
			weixin_logn: function() {

				let _this = this
				uni.login({
					provider: 'weixin',
					success(res) {
						let data = {
							code: res.code
						}
						_this.wangluo_qingqiu('/wx/user/login', data, false, true, 'GET').then(value => {
							if (value) {
								getApp().globalData.public_user_token = value.token
								console.log(value.token)
								getApp().globalData.public_login_status = true
								getApp().globalData.public_user_phone = value.has_phone

								if (value.special_value == 1) {
									getApp().globalData.public_wxapp_type = true
								} else {
									getApp().globalData.public_wxapp_type = false

								}

								_this.huoqu_souye_xinxi()
							}
						})
					}
				})

			},
			huoqu_souye_xinxi: function() {
				let _this = this
				uni.getLocation({
					type: 'wgs84',
					success: function(res) {
						console.log('当前位置的经度：' + res.longitude);
						console.log('当前位置的纬度：' + res.latitude);
						let data = {
							longitude: res.longitude,
							dimension: res.latitude
						}
						// _this.wangluo_qingqiu('/wx/ipms/getnearestquarters', data, true, true, 'GET').then(value => {
						// 	if (value) {

						// 		getApp().globalData.longitude = res.longitude
						// 		getApp().globalData.latitude = res.latitude
						// 		getApp().globalData.public_user_property_data = value
						// 		getApp().globalData.public_user_property_id = value.quartersId
						// 		uni.setNavigationBarTitle({
						// 			title: value.quartersName
						// 		});
						// 		_this.huoqu_xiaoqu_lunbo()
						// 		_this.huoqu_xiaoqi_xinwen()
						// 		_this.huoqu_xiaoqi_bangding()


						// 		// console.log(value)
						// 		// _this.list = value
						// 	}
						// })

					},
					fail: function() {
						let data = {
							longitude: 0,
							dimension: 0
						}
						_this.wangluo_qingqiu('/wx/ipms/getnearestquarters', data, true, true, 'GET').then(value => {
							if (value) {
								// console.log(value)
								// _this.list = value


								getApp().globalData.longitude = "0"
								getApp().globalData.latitude = "0"
								getApp().globalData.public_user_property_data = value
								getApp().globalData.public_user_property_id = value.quartersId
								uni.setNavigationBarTitle({
									title: value.quartersName
								});
								_this.huoqu_xiaoqu_lunbo()
								_this.huoqu_xiaoqi_xinwen()
								_this.huoqu_xiaoqi_bangding()

							}
						})
					}
				});

				// console.log("index")

			},
			huoqu_xiaoqu_lunbo: function() {
				let _this = this
				let data = {
					quartersId: getApp().globalData.public_user_property_id,
				}
				_this.wangluo_qingqiu('/wx/ipms/getnotices', data, true, true, 'GET').then(value => {
					if (value) {
						_this.lunbo_list = value
					}
				})
			},
			huoqu_xiaoqi_xinwen: function() {
				let _this = this
				let data = {
					quartersId: getApp().globalData.public_user_property_id,
				}
				_this.wangluo_qingqiu('/wx/ipms/getnoticelist', data, true, true, 'GET').then(value => {
					if (value) {
						_this.xinwen_list = value
					}
				})
			},
			huoqu_xiaoqi_bangding: function() {
				// console.log("获取绑定信息")
				let _this = this
				let data = {}
				_this.wangluo_qingqiu('/wx/ipms/getRoomsUnderOwner', data, true, true, 'GET').then(value => {
					if (value.length > 0) {
						getApp().globalData.public_user_property = true
					}
				})

			}
		},
		onPullDownRefresh: function() {
			let _this = this;
			_this.huoqu_souye_xinxi()
			uni.stopPullDownRefresh();
		},
		onShareAppMessage(res) {
			return {
				title: "晨夜车管家",
				path: '/pages/index/index',
				imageUrl: '/static/img/logo.png',
			}
		},
		onShow: function() {
			if (getApp().globalData.public_user_token) {
				this.huoqu_souye_xinxi()
			}
			
			this.debug_show = getApp().globalData.public_tiaoshi_moshi
		}

	};
</script>

<style>
	.duan {
		margin-top: -16rpx;
	}

	page {
		background: #FFFFFF;
	}
</style>
